.gameSubmit {
    /* box-shadow: 0 0 20px 5px white; */
    margin: 150px 8vw;
}

.page-move {
    position: relative;
    float: left;
}

.page-move:hover {
    transform: scale(1.5);
}

.gameSubmit-title {
    padding: 10px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    text-align: center;
    color: black;
    font-weight: bold;
    font-size: 20px;
    font-family: "Microsoft Sans Serif";
}

.title {
    text-align: center;
}

.gameSubmit-main {
    background-color: #dadada;
    padding: 4vh 1.2vw 6vh 1.2vw;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    box-shadow: 0 0 20px 5px var(--hover_clr);
    justify-content: space-around;
}

.upload {
    padding: 0 7vw;
}

.gamesub-content-block {
    width: 50%;
    line-height: 10px;
}

.game-cover-btn:hover i {
    color: #d13737;
}

.gamesub-content-block-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
}

.gamesub-content-block-item>span {
    color: black;
    font-weight: bold;
    margin-bottom: 10px;
}

.gamesub-content-block-input {
    background-color: transparent;
    outline: none;
    border: 2px solid rgb(0, 0, 0);
    border-radius: 5px;
    font-size: 16px;
    padding: 10px 5px;
    width: 80%;
    box-shadow: 0 0 2px 3px black;
    color: rgb(0, 0, 0);
    margin-bottom: 30px;
}

.gamesub-content-block-item area {
    height: 100px !important;
}

.gamesub-content-block-input:hover {
    border: 2px solid black;
    box-shadow: 0 0 2px 3px black;
}

.gamesub-content-block-input:focus {
    border: 2px solid black;
    box-shadow: 0 0 2px 3px black;
}

.gamesub-content-block-item {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1200px) {
    .gameSubmit {
        padding-left: 5% !important;
    }
}

@media screen and (max-width: 901px) {
    .gamesub-content-block-item {
        width: 125%;
        padding-top: 20px;
    }

    .gameSubmit-main {
        padding: 4vh 5vw;
    }
}

.gamesub-upload-block {
    width: 30%;
    display: flex;
    flex-direction: column;
}

.gamesub-upload-preview {
    width: 100%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    padding: 20px 0px;
    color: black;
}

.gamesub-upload-preview>i {
    margin: 30px 0px;
    font-size: 150px;
    color: black;
}

.gamesub-upload-preview>img {
    width: 100%;
    height: 100%;
}

.borderblockclr {
    border: 2px dashed black;
}

.bordererrclr {
    border: 2px solid var(--block_clr) !important;
}

.game-cover-btn-block {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.game-cover-btn {
    padding: 20px 30px;
    background-color: white;
    font-size: 20px;
    border: 1px solid var(--block_clr);
    border-radius: 5px;
    cursor: pointer;
    color: black;
}

.game-cover-btn:hover {
    background-color: white;
    color: black;
    box-shadow: 0 0 3px 3px var(--block_clr);
    font-weight: bold;
}

.game-cover-btn>i {
    margin-right: 10px;
}

.game-cover-btn:hover i {
    color: black;
}

.gamesub-content-submit-btn {
    text-align: center;
    width: fit-content;
    background-color: white;
    padding: 15px 50px;
    font-size: 20px;
    border-radius: 5px;
    cursor: pointer;
    color: black;
    border: 1px solid var(--block_clr);
}

.gamesub-content-submit-btn img {
    height: 20px;
}

.gamesub-content-submit-btn:hover {
    background-color: white;
    color: black;
    box-shadow: 0 0 3px 3px var(--block_clr);
    font-weight: bold;
}

.gamesub-content-submit-btn :hover span {
    background-color: var(--white);
    color: var(--block_clr);
    font-weight: bold;
    border-color: var(--block_clr);
    box-shadow: 0 0 3px 3px var(--block_clr);
    font-weight: bold;
}

.submitIMG {
    max-height: 338px;
    max-width: 600px;
}

.gamesub-content-block-item-one {
    font-size: 24px !important;
    padding: 10px 0;
    color: var(--white);
}

.gamesub-content-block-item-sec {
    color: rgb(116, 116, 116) !important;
    font-size: 22px !important;
    margin-bottom: 30px !important;
}

.apikey-modal-card {
    padding: 30px;
    font-size: 19px;
    /* background-color: var(--menu_backclr); */
    color: var(--white);
}

.api_key {
    cursor: pointer;
    color: var(--white) !important;
}

.css-1t1j96h-MuiPaper-root-MuiDialog-paper {
    background: linear-gradient(to right, black, var(--menu_backclr));
}